body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#light {
  height: 100vh;
  width: 100vw;
  max-width: 100%;
  background-color: #ffffff;
}

#dark {
  height: 100vh;
  width: 100vw;
  max-width: 100%;
  background-color: #312f31;
}

.App {
  position: relative;
  overflow: hidden;
}

.home-app {
  display: flex;
  margin: 10px;
}

.switch {
  float: left;
  padding-top: 1rem;
  padding-left: 1rem;
  justify-content: right;
  align-items: right;
}

.switch-label {
  margin-bottom: 1rem;
}

.switch label {
  margin: 10px;
}

#light .switch label {
  color: black;
}

#dark .switch label {
  color: white;
}


.color-nav {
  height: 60px;
}

.nav-title {
  color: white;
}

.phf-logo {
  width: 15%;
}

.reg {
  border-radius: 8px;
  cursor: pointer;
  border: 0;
  font-weight: 500;
  margin: 0 7px;
  width: 100px;
  padding: 8px 0;
  box-shadow: 0 0 20px rgba(104, 85, 224, 0.2);
  transition: 0.4s;
}

.log {
  border-radius: 8px;
  cursor: pointer;
  border: 0;
  font-weight: 500;
  margin: 0 7px;
  width: 230px;
  padding: 8px 0;
  box-shadow: 0 0 20px rgba(104, 85, 224, 0.2);
  transition: 0.4s;
}

.reg {
  border-radius: 20px;
  color: rgb(255, 255, 255);
  background-color: #00539b;
  border: 1px solid #00539b;
}


.log {
  border-radius: 20px;
  color: #00539b;;
  background-color: rgba(255, 255, 255, 1);
  border: 1px solid #00539b;;
}

.log:hover {
  color: white;
  box-shadow: 0 0 20px #55adfa;
  background-color: #00539b;
}

.reg:hover {
  color: #00539b;
  box-shadow: 0 0 20px #8dc7fa;
  background-color: white;
}

.dash-container {
  padding-left: 1rem;
  padding-bottom: 5rem;
  width: 95%;
}

.dash-results {
  padding: 1rem;
}

.card-caption {
  color: black;
}

.modal-backdrop {
  background-color: rgba(61, 61, 61, 0.233) !important;
}
.home-container {
  width: 100%;
  height: 100%;
  padding-bottom: 12rem;
  clear: both;
}

.content-code {
  position: relative;
  padding-right: 3.5rem;
  clear: both;
}

#light .content-info {
  color: black;
  width: 80%;
  margin: 0 auto;
}
#dark .content-info {
  color: white;
  width: 80%;
  margin: 0 auto;
}

#light .content-code {
  color: black;
  width: 80%;
  margin: 0 auto;
}
#dark .content-code {
  color: white;
  width: 80%;
  margin: 0 auto;
}

.code-dropdown {
  padding-bottom: 1rem;
}

.code-search {
  padding-bottom: 1rem;
}

.search-bar {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.search-term {
  width: 80%;
  border: 1px solid grey;
  height: 50px;
  outline: none;
  color: #000000;
}

.search-button {
  float: right;
  width: 20%;
  padding: 11px;
  background: #00539b;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none; /* Prevent double borders */
  cursor: pointer;
}

.next-button {
  float: left;
  width: 20%;
  padding: 11px;
  background: #00539b;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none; /* Prevent double borders */
  cursor: pointer;
}

.search-button:hover {
  background: #078bff;
}

.out-ph {
  float: right;
  padding-right: 10rem;
  -webkit-transform: translateY(200%);
          transform: translateY(200%);
}

.code-editor {
  padding-bottom: 1rem;
}

.code-nextAnswer {
  padding-bottom: 1rem;
}


.login-bg-wrapper {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.forgotlink {
  color: blue;
}

.login-img {
  border-radius: 25px;
}

.reg-login {
  border-radius: 8px;
  cursor: pointer;
  border: 0;
  font-weight: 500;
  margin: 0 7px;
  width: 150px;
  padding: 8px 0;
  box-shadow: 0 0 20px rgba(104, 85, 224, 0.2);
  transition: 0.4s;
}

.reg-login {
  border-radius: 20px;
  color: rgb(255, 255, 255);
  background-color: #00539b;
  border: 1px solid #00539b;
}

.reg-login:hover {
  color: #00539b;
  box-shadow: 0 0 20px #8dc7fa;
  background-color: white;
}

.error-modal-body {
  background-color: #f5d4d8;
}

.error-text {
  color: #722e36
}

.footer-title {
  padding-left: 5px;
  color: white;
}

p {
  color: white;
}

a {
  color: white;
  text-decoration: none;
}

.link-footer {
  padding-bottom: 5px;
}

.shadow {
  background-color: #262626;
}
